export const validateLandRegisterCode = (ekw, ekwData) => {
  ekw = ekw.toUpperCase()
  let result = ''
  if (ekw === '' || !ekw) {
    result += 'Numer ksigi wieczystej nie moe by pusty<br/>'
  }

  const splitted = ekw.split('/')
  if (splitted.length !== 3) {
    result += 'Nieprawidowa liczba sekcji numeru ksigi wieczystej, powinny by 3 oddzielone znakiem /<br/>'
    return result
  }
  if (splitted[0].length !== 4 || splitted[1].length !== 8 || splitted[2].length !== 1) {
    result += 'Nieprawidowa dugo ktrej z sekcji numeru ksigi wieczystej<br/>'
  }

  if (!ekwData.regions.includes(splitted[0])) {
    result += 'Nie ma takiego kodu Wydziau Ksig Wieczystych Sdu Rejonowego<br/>'
  }
  let controlSum = 0
  let idx = 0
  for (let i = 0; i < splitted[0].length; i++) {
    controlSum += ekwData.weights[splitted[0][i]] * ekwData.multipliers[idx++]
  }
  for (let i = 0; i < splitted[1].length; i++) {
    controlSum += ekwData.weights[splitted[1][i]] * ekwData.multipliers[idx++]
  }
  if (isNaN(parseInt(splitted[1]))) {
    result += 'Druga sekcja numeru ksigi wieczystej powinna by dan liczbow<br/>'
  }

  if (isNaN(parseInt(splitted[2]))) {
    result += 'Cyfra kontrolna numeru ksigi wieczystej powinna by dan liczbow<br/>'
    return result
  }

  if (controlSum % 10 !== parseInt(splitted[2])) {
    result += 'Niepoprawna suma kontrolna<br/>'
  }
  return result
}
